	module.exports = {"data":{"markdownRemark":{"html":"<p>\"Fake Happy\" is a song by American rock band Paramore. It was released on August 29, 2017 through Fueled by Ramen as the third single off their fifth studio album After Laughter (2017). Written by lead vocalist Hayley Williams and guitarist Taylor York and produced by Justin Meldal-Johnsen and York, the song was recorded in the band's hometown, Nashville, Tennessee.</p>\n<h2>Background</h2>\n<p>Discussing the inspiration behind the song, Williams stated: \"Well, I mean, the title is pretty self-explanatory. I think there's a lot of time we go out or we do things and we don't feel the way that we project, you know, that we wanna look like we feel...\" In a Beats 1 interview with Zane Lowe, Williams also said regarding the song: \"I hate phoniness. It's not fun to be around, it's not fun to do yourself. But then there are these moments in your life where you're professional and you have to have grace with yourself, you have to have grace with other people and work hard, but it's that self-preservation thing.</p>","frontmatter":{"title":"Hard Times","path":"/hard-times","color":"#20EAE7","image":"https://i.ytimg.com/vi/AEB6ibtdPZc/maxresdefault.jpg"}}},"pathContext":{}}

